export default {
  data () {
    return {
      list: '',
      content: ''
    };
  },
  components: {},
  computed: {},
  methods: {},
  onLoad(e){
    this.$store.dispatch('getMessageDetail',{
      id: e.id
    }).then(res=>{
      this.list = res.data
      this.content = res.data.content.replace(/\s+style="[^"]*"/g,'')
        .replace(/<p>/ig, '<p class="p_class">')
        .replace(/\<img/gi, '<img class="img_class" style="width:100% !important;height:auto" ')
    }).catch(err=>{})
  }
}

